.section-title {
  font-size: 3.6rem;
  line-height: 1.3;
  font-weight: 600;
  margin-bottom: 30px;

  &--lg {
    font-size: 4.8rem;
  }

  &--xl {
    font-size: 6.4rem;
  }

  &.has-bg-gradient {
    //background: linear-gradient(90deg, #00423C 0%, #24EC7E 100%);
    background: linear-gradient(90deg, #00423C -12.8%, #24EC7E 79.21%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    color: transparent;
  }
}

.subtitle {
  display: inline-block;
  padding: 10px 24px;
  background-color: #AFF8C8;
  border-radius: 4px;
  text-transform: uppercase;
  text-align: center;
  font-size: 1.4rem;
  font-weight: 500;
  margin-bottom: 30px;
}

.title-row {
  margin-bottom: 50px;

  p {
    font-size: 2rem;
    line-height: 1.4;
  }
}

.card-pricing,
.payment-methods,
.modular-solutions,
.card-choose-card-reader {
  ul {
    @extend .list-unstyled;

    li {
      position: relative;
      padding-left: 20px;

      + li {
        margin-top: 10px;
      }

      &::before {
        content: '';
        position: absolute;
        left: 0;
        top: 5px;
        width: 15px;
        height: 15px;
        background: url(../img/other/check-icon.svg) center left no-repeat;
        background-size: 12px;
      }
    }
  }
}

// < 1200px
@include media-breakpoint-down(xl) {
  .section-title {
    &--xl {
      font-size: 3.6rem;
    }
  }
  main br {
    display: none;
  }
}

// < 768px
@include media-breakpoint-down(md) {
  .section-title {
    font-size: 2.8rem;
    margin-bottom: 20px;

    &--lg {
      font-size: 3.2rem;
    }

    &--xl {
      font-size: 3.6rem;
    }
  }

  .title-row {    
    p {
      font-size: 1.6rem;
    }
  }
}