  display: grid;
  gap: 20px;
  counter-reset: step;
  margin-bottom: 50px;
}
.component-content .key-features--item {
  position: relative;
  padding-left: 50px;
}
.component-content .key-features--item::before {
  position: absolute;
  left: 0;
  content: counter(step);
  counter-increment: step;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 35px;
  height: 35px;
  background: #097152;
  color: #fff;
  font-size: 2rem;
  font-weight: 600;
  border-radius: 50%;
}
.component-content .key-features--item h4, .component-content .key-features--item .h4 {
  font-size: 2rem;
  font-weight: 600;
  margin-bottom: 20px;
}
.component-content .key-features--item p {
  position: relative;
  padding: 0 0 10px;
  margin: 0;
}
.component-content .key-features--item p::before {
  content: "";
  position: absolute;
  left: -35px;
  top: 5px;
  bottom: 0;
  width: 3px;
  background: #097152;
}

